.movie-container {
  width: 40%;
  height: 630px;
  margin: 25px;
  padding: 10px;
  background-color: rgb(22, 22, 22);
  box-shadow: 0 -2px 10px rgba(0, 0, 0, 1);
}

img {
  display: block;
  margin: 0px auto;
}

.movie_title {
  margin-top: 10px;
  margin-bottom: 5px;
  text-align: center;
  font-size: 24px;
  font-weight: 700;
  height: 58px;
  overflow-y: scroll;
  display: flex;
  justify-content: center;
  align-items: center;
}

.title_text {
  max-height: 58px;
}

.movie_year {
  margin: 5px 0px;
  font-weight: 300;
  text-align: center;
}

.movie_summary {
  height: 85px;
  overflow-y: scroll;
  font-size: 14px;
}

.movie_info {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 45px;
  overflow-y: scroll;
}

.info {
  font-size: 12px;
  font-weight: 300;
  margin-right: 15px;
}
