body {
  margin: 20px 20px;
  background-color: #080808;
  color: #f2f2f2;
}

html,
body,
#root,
.container {
  height: 100%;
}

.movies {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
